import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { MatSnackBar, MatSnackBarRef, TextOnlySnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'playground-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    class: 'd-flex flex-column block-100'
  }
})
export class AppComponent implements OnInit {

  constructor(
    private readonly snackBar: MatSnackBar,
  ) {}

  ngOnInit(): void {
    if ('pwaInstallEvents' in globalThis) { // Defined in the index.html
      this.handlePwaInstallEvents();
    }
  }

  private async handlePwaInstallEvents(): Promise<void> {
    let pwaInstallDialog: MatSnackBarRef<TextOnlySnackBar> | undefined = undefined;
    let pwaInstalled = false;

    pwaInstallEvents.installed.then(installed => {
      pwaInstalled = installed;

      if (pwaInstallDialog) {
        pwaInstallDialog.dismiss();
      }
    });

    const beforeInstallPrompt = await pwaInstallEvents.beforeInstallPrompt;

    pwaInstallDialog = this.showPwaInstallDialog();

    pwaInstallDialog.onAction().subscribe(() => {
      if (!pwaInstalled) {
        beforeInstallPrompt.prompt();
      }
    });
  }

  private showPwaInstallDialog(): MatSnackBarRef<TextOnlySnackBar> {
    return this.snackBar.open(
      $localize`:@@installPwaMessage:Try i18n app.`,
      $localize`:@@installPwaButton:Install`,
      {
        politeness: 'polite',
        duration: 1000 * 7, // Hide after 7 seconds.
      },
    );
  }

}
